import {
    GameRandom,
    Inject,
    ISimpleControllerConfiguration,
    SimpleBaseController
} from "@denishussar/html5-game-engine";
import { SoundConstants, SoundLevels } from "./constants/SoundConstants";
import { Utils } from "./controllers/Common/Utils";
import { BoxController } from "./controllers/Game/BoxController";
import { MainScreenController } from "./controllers/Game/MainScreenController";
import { PreviewSceneController } from "./controllers/Game/PreviewSceneController";
import { StoreSceneController } from "./controllers/Game/StoreScreenController";
import { GameLogic } from "./logic/GameLogic";
import { GameModel, GameStateEnum } from "./models/GameModel";
import { Item } from "./models/Item";

export interface IRootControllerOptions {
    version: string;
}

export class RootController extends SimpleBaseController {
    $model: GameModel;

    public constructor(@Inject private _options: IRootControllerOptions,
                       @Inject private _random: GameRandom,
                       @Inject private _gameLogic: GameLogic) {
        super("RootController");
    }

    protected getConfiguration(): ISimpleControllerConfiguration {

        return {
            childControllers: {
                mainSceneController: {
                    controller: MainScreenController,
                    model: this.$model
                },
                storeSceneController: {
                    controller: StoreSceneController,
                    model: this.$model
                },
                boxController: {
                    controller: BoxController,
                    model: this.$model
                },
                previewController: {
                    controller: PreviewSceneController,
                    model: this.$model
                }
            },
            modelEvents: {
            },
            resources: {},
            modelWatch: {},
            sounds: [],
        };
    }

    protected async onInitialize() {
        this._gameLogic.changeGameState(GameStateEnum.preview);
        this.soundManager.play(SoundConstants.AMBIENT, true, SoundLevels.levelAmbient);
        this.setupItems();
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    private setupItems(): void {
        const length = 24;
        for (let i = 0; i < length; i++) {
            const id = `${i + 1}`;
            this.$model.itemsOnShelves.push(new Item(id, this.json.pictures[id]['desc']));
        }

        Utils.shuffle(this.$model.itemsOnShelves);

        for (let i = 0; i < 5; i++) {
            this.$model.itemsInTheBox.push(this.$model.itemsOnShelves.pop());
        }
    }

    private json = {
        "pictures": {
          "1": {
            "desc": "Самые уютные вечера проходили в компании теплого чая,\n пряжи и спокойного кантри"
          },
          "2": {
            "desc": "Мы нашли их совершенно случайно в одной из наших поездок,\n с тех пор он их не снимал"
          },
          "3": {
            "desc": "Как же он гордился своим первым творением из кожи!\n Он подарил этот кошелек мне на годовщину"
          },
          "4": {
            "desc": "Его зубная щетка до сих пор стояла в стаканчике на раковине"
          },
          "5": {
            "desc": "Каждый Новый год я вязала для каждого члена семьи \nпо паре теплых носочков"
          },
          "6": {
            "desc": "Ох, а этим я задирала мальчишек в школе.. \nХулиганкой же я была"
          },
          "7": {
            "desc": "Старые красивые ножницы, которые достались мне от бабушки. \nОни должно быть старше меня…"
          },
          "8": {
            "desc": "В тот год выдалась холодная зима, \nон никуда не выходил без этого шарфа, \nкоторый я связала для него"
          },
          "9": {
            "desc": "Мы привозили десятки фотографий из наших путешествий!"
          },
          "10": {
            "desc": "Этого зайчика я сшила как прощальный подарок внучке, \nкогда они собрались переезжать. \nЖаль, что я так и не успела его отдать"
          },
          "11": {
          "desc": "Дети пытались приучить нас к каким-то современным технологиям. \nМы любили по вечерам на веранде танцевать под эту музыку"
          },
          "12": {
          "desc": "Подарив этот медальон, он сказал, \nчто выбирал его весь день на какой-то винтажной барахолке, \nха-ха! \nНо этот подарок будет всегда бесконечно дорог мне"
          },
          "13": {
          "desc": "Наша первая совместная фотография. \nВ тот вечер мы пошли в поход компанией друзей \nи на той скале был виден потрясающий закат"
          },
          "14": {
          "desc": "Когда он уезжал в долгие поездки по работе, \nмы не пропускалини дня, чтоб не написать друг другу письмо"
          },
          "15": {
          "desc": "А эту записную книгу он подарил мне, \nкогда увидел как сильно я увлеклась садоводством и травами. \nВсегда носила ее с собой, чтоб успеть записать любую мысль"
          },
          "16": {
          "desc": "Ох, это письмо, в котором он впервые признался мне в любви… \n Он был тем еще романтиком"
          },
          "17": {
          "desc": "На день рождения сына мы устроили пикник в парке. \nКак же они тогда забегались, \nк вечеру ни у кого не осталось сил"
          },
          "18": {
          "desc": "Ключи от домика на дереве, \nкоторый он построил для наших детей"
          },
          "19": {
          "desc": "Это был прекрасный день на пляже со всей семьей! \nХах, помню как мы все ловили эту шляпку, когда ее унесло ветром"
          },
          "20": {
          "desc": "Как же он любил читать! \nЯ каждый вечер ругалась, \nчтобы он включал свет поярче и не портил глаза"
          },
          "21": {
              "desc": "Как жаль, что мои колени уже не те… \nбольше не повозишься в саду"
            },
            "22": {
              "desc": "Я любила проводить время в саду, \nмы всегда вместе высаживали по весне львиный зев. \nГде же я потеряла вторую перчатку…?"
            },
            "23": {
              "desc": "Мы вдвоем поехали за город, погулять в лесу. \nБыло так замечательно! \nСвежий воздух, тишина, запах ели… тогда он собрал для меня эти цветы"
            },
            "24": {
              "desc": "Тогда мы с нашими ребятишками пошли на урок гончарного дела, \nони сделали для нас милые парные кружки. \nХоть моя уже и износилась, я до сих пор пользовалась ею"
            },
            "25": {
              "desc": "ashes to ashes..."
            }
        }
      }
      
}
